










import { Component, Vue } from 'vue-property-decorator';
import GameVue from './components/Game.vue';
import Game from './engine/Game';
import Room from './engine/Room';
import {PuzzleCode} from './engine/Puzzle';
import { ActionRoom, ActionMsg } from './engine/Action';


@Component({
    components: {
        GameVue,
    },
})
export default class App extends Vue {

    game: Game|null = null;

    mounted(){

        this.game = new Game();

        const r0 = new Room("Room0", 'open');
        r0.add("Aquí no hay nada, la puerta está abierta, pasa la sala A");

        const rA = new Room("RoomA", 'open');
        const rB = new Room("RoomB");


        rA.add("La Room A tiene una puerta con código");
        rA.add("El código es 123")
        
        const rA_p1 = new PuzzleCode('123');
        const rA_p1_a1 = new ActionRoom(rB, 'open');
        rA_p1.add(rA_p1_a1);
        rA_p1.add(new ActionMsg("Muy bien, tus habilidades investigadores superiores te han permitido solucionar la prueba"));
        rA.add(rA_p1);

        rB.add("Has ganado")


        this.game.add(r0);
        this.game.add(rA);
        this.game.add(rB);


    }

}

