import { Action } from './Action';

export class Puzzle {

    actions: Action[] = [];

    constructor(){

    }

    validate(solution: any): boolean {
        return false;
    }

    add(action: Action){
        this.actions.push(action);
    }

    run(){
        this.actions.forEach(a => a.run());
    }

}



export class PuzzleCode extends Puzzle {

    code: string;

    constructor(code: string){
        super();
        this.code = code;
    }

    validate(solution: any){
        return this.code === solution;
    }

}